--            LOADER            --
**********************************/
.contenedor-loader {
  background-color: #f3f3f3;
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes loaderAnimation {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}
.loader::after, .loader::before, .loader {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation: loaderAnimation 1.5s infinite ease-in-out;
}

.loader::after, .loader::before {
  position: absolute;
  top: 0;
}

.loader {
  color: #4685fa;
  font-size: 13px;
  position: relative;
  animation-delay: -0.16s;
}
.loader::before {
  content: "";
  left: -3.5em;
  animation-delay: -0.32s;
}
.loader::after {
  content: "";
  left: 3.5em;
}

.navbar {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background: #f5efe0;
}
@media (min-width: 480px) {
  .navbar {
    flex-direction: row;
  }
}
.navbar .logo {
  width: 30%;
}
@media (min-width: 480px) {
  .navbar .logo picture {
    max-width: 20%;
  }
}
.navbar .aparecer {
  display: block !important;
}
.navbar .menu {
  display: none;
}
@media (min-width: 480px) {
  .navbar .menu {
    display: block;
  }
}
.navbar .menu ul {
  display: flex;
